import React, { Component, useEffect, useState } from 'react';
import { Link, useHistory } from "react-router-dom";
import { Input, CustomInput, Col } from 'reactstrap';
import { signIn } from './AuthFunction.js';
import FormValidator from '../Common/FormValidator.js';
import swal from 'sweetalert';

class Login extends Component {
    constructor(props) {
        super(props)
    }

    state = {
        formLogin: {
            email: '',
            password: ''
        }
    }
     /**
      * Validate input using onChange event
      * @param  {String} formName The name of the form in the state object
      * @return {Function} a function used for the event
      */
    validateOnChange = event => {
        const input = event.target;
        const form = input.form
        const value = input.type === 'checkbox' ? input.checked : input.value;

        const result = FormValidator.validate(input);

        this.setState({
            [form.name]: {
                ...this.state[form.name],
                [input.name]: value,
                errors: {
                    ...this.state[form.name].errors,
                    [input.name]: result
                }
            }
        });

    }

    onSubmit = e => {
        const form = e.target;
        const inputs = [...form.elements].filter(i => ['INPUT', 'SELECT'].includes(i.nodeName))

        const { errors, hasError } = FormValidator.bulkValidate(inputs)

        this.setState({
            [form.name]: {
                ...this.state[form.name],
                errors
            }
        });

        console.log(hasError ? 'Form has errors. Check!' : 'Form Submitted!')
        e.preventDefault()

        this.connexion()
    }

    connexion = () => {
        const email =this.state.formLogin.email
        const password = this.state.formLogin.password
        console.log(email, password)
        signIn(email, password)
        .then((response) => {
            this.props.history.push('/')
        })
        .catch ((error) => {
            console.log("error", error);
            swal("Connexion Failed", error.message , "warning")
        })
    }

    /* Simplify error check */
    hasError = (formName, inputName, method) => {
        return  this.state[formName] &&
                this.state[formName].errors &&
                this.state[formName].errors[inputName] &&
                this.state[formName].errors[inputName][method]
    }

    render() {
        return (
        <div className='row'>
            {/*<img src="img/Businessman-pana.png" alt="Fonctionnalite indisponible" width="60%" height="70%" />*/}
            <div className="block-center mt-4 wd-xl ">
                <div className="card card-flat">
                    <div className="card-header text-center bg-dark">
                        <a href="">
                            <em className="fas fa-recycle text-success fa-1x"></em>
                        </a>
                    </div>
                    <div className="card-body">
                        <p className="text-center py-2">Au plaisir de vous revoir !</p>
                        <form className="mb-3" name="formLogin" onSubmit={this.onSubmit}>
                            <div className="form-group">
                                <div className="input-group with-focus">
                                    <Input type="email"
                                        name="email"
                                        className="border-right-0"
                                        placeholder="Enter email"
                                        invalid={this.hasError('formLogin','email','required')||this.hasError('formLogin','email','email')}
                                        onChange={this.validateOnChange}
                                        data-validate='["required", "email"]'
                                        value={this.state.formLogin.email}/>
                                    <div className="input-group-append">
                                        <span className="input-group-text text-muted bg-transparent border-left-0">
                                            <em className="fa fa-envelope"></em>
                                        </span>
                                    </div>
                                    { this.hasError('formLogin','email','required') && <span className="invalid-feedback">Veuillez renseigner ce champ</span> }
                                    { this.hasError('formLogin','email','email') && <span className="invalid-feedback">Cette adresse n'est pas valide</span> }
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="input-group with-focus">
                                    <Input type="password"
                                        id="id-password"
                                        name="password"
                                        className="border-right-0"
                                        placeholder="Password"
                                        invalid={this.hasError('formLogin','password','required')}
                                        onChange={this.validateOnChange}
                                        data-validate='["required"]'
                                        value={this.state.formLogin.password}
                                    />
                                    <div className="input-group-append">
                                        <span className="input-group-text text-muted bg-transparent border-left-0">
                                            <em className="fa fa-lock"></em>
                                        </span>
                                    </div>
                                    <span className="invalid-feedback">Veuillez renseigner ce champ</span>
                                </div>
                            </div>
                            <div className="clearfix">
                                <CustomInput hidden type="checkbox" id="rememberme"
                                    className="float-left mt-0"
                                    name="remember"
                                    label="Remember Me">
                                </CustomInput>
                                <div className="float-right">
                                    <Link to="recover" className="text-muted">Mot de passe oubli√©?</Link>
                                </div>
                            </div>
                            <button className="btn btn-block btn-primary mt-3" type="submit">Connexion</button>
                        </form>
                    </div>
                </div>
            </div>
        
        </div>
        );
    }
}

export default Login;
